import React from "react";
import { useHistory } from "react-router-dom";

export default function Menu({ activePage = "home" }) {
  return (
    <div className="menu">
      <div className="menu-header">
        <img
          width={60}
          height={70}
          className="menu-logo"
          src="./assets/images/logo.png"
          alt=""
        />
      </div>
      <div className="menu-items-container">
        <MenuItem
          svgIcon="dashboard.svg"
          name="Dashboard"
          path="/home"
          isActive={activePage == "home"}
        />

        <MenuItem
          svgIcon="dyalog.svg"
          name="Audit Logs"
          path="/audit"
          isActive={activePage == "audit"}
        />

        <MenuItem
          svgIcon="settings.svg"
          name="Settings"
          path="/settings"
          isActive={activePage == "settings"}
        />
        <MenuItem
          svgIcon="logout.svg"
          name="Logout"
          path="/"
          isActive={false}
        />
      </div>
    </div>
  );
}

function MenuItem({ svgIcon, name, path, isActive = false }) {
  const history = useHistory();
  return (
    <div
      className={isActive ? "menu-items menu-items-active" : "menu-items"}
      onClick={() => {
        history.push(path);
      }}
    >
      <div className="menu-item">
        <img width={20} height={20} src={"./assets/svg/" + svgIcon} alt="" />
        &nbsp; {name}
      </div>
    </div>
  );
}
